import React from 'react';
import { Link } from 'react-router-dom';

export default function Documentation(props) {
  return (
    <>
    <div className = "container text-center">
      <br/>
      <h1>
        KarlJoke
      </h1>
      <h3>Free DadJokes API</h3>
      {/*Available Endpoints*/}
      <h5 className="text-start">Get a random joke &rarr;</h5>
      <pre className="language-css"><code>  GET /jokes/random</code></pre>
      <h5 className="text-start">Get ten random jokes &rarr;</h5>
      <pre className="language-css"><code>  GET /jokes/ten</code></pre>
      <h5 className="text-start">Get number of random jokes&rarr;</h5>
      <pre className="language-css"><code>  GET /jokes/3<br/>       /jokes/24</code></pre>
      <h5 className="text-start">Get random joke by type &rarr;</h5>
      <pre className="language-css"><code>  GET /jokes/programming/random<br/>       /jokes/general/random</code></pre>
      <h5 className="text-start">Get number of random jokes by type &rarr;</h5>
      <pre className="language-css"><code>  GET /jokes/programming/4<br/>       /jokes/knock-knock/6</code></pre>
      <h5 className="text-start">Get joke by type and id &rarr;</h5>
      <pre className="language-css"><code>  GET /jokes/general/211</code></pre>
      <h2>Have a joke? Why not share with us! Make a PR <a href="https://github.com/eklavyadev/karljoke" target="_blank">here</a> and I will do next
      </h2>
      </div>
    </>
  )
}
